












import Vue, { VueConstructor } from 'vue'
import { mapState, mapActions } from 'vuex'
import { RootState } from '@/store'
import { PlantTag } from '@/types/plant'

interface TagsMapState {
  allTags: {
    data: PlantTag[]
    loaded: boolean
  }
}

export default (Vue as VueConstructor<Vue & TagsMapState>).extend({
  name: 'SettingsTags',
  computed: {
    ...mapState<RootState>({
      allTags: (state: RootState) => state.tags,
    }),
  },
  methods: {
    ...mapActions({ loadTags: 'tags/loadTags' }),
  },
  async created() {
    if (!this.allTags.loaded) {
      await this.loadTags()
    }
  },
})
