



































































































































import Vue, { VueConstructor } from 'vue'
import { mapActions, mapState } from 'vuex'
import { RootState } from '@/store'
import config from '@/config'
import delay from '@/utils/promiseDelay'
import { getLocalEntry } from '@/services/localStorage'
import { Plant } from '@/types/plant'

// TODO: Menu flickers when coming from another route. Possibly because of how the router is setup.

interface MenuMapState {
  userName: string
  email: string
  userPhotoURL: string
  plants: Plant[]
}

export default (Vue as VueConstructor<Vue & MenuMapState>).extend({
  name: 'Settings',

  components: {
    'feather-arrow': () =>
      import('vue-feather-icons/icons/ArrowRightIcon' /* webpackChunkName: "icons" */),
    'feather-smile': () =>
      import('vue-feather-icons/icons/SmileIcon' /* webpackChunkName: "icons" */),
    'feather-logout': () =>
      import('vue-feather-icons/icons/LogOutIcon' /* webpackChunkName: "icons" */),
    'feather-external': () =>
      import('vue-feather-icons/icons/ExternalLinkIcon' /* webpackChunkName: "icons" */),
  },

  data: () => ({
    logoutRedirect: false,
    version: config.version,
    plantCountFromLocalStorage: getLocalEntry(config.localStorage.plantCount),
  }),

  computed: {
    ...mapState<RootState>({
      userName: (state: RootState) => state.account.displayName,
      email: (state: RootState) => state.account.email,
      userPhotoURL: (state: RootState) => state.account.photoURL,
      plants: (state: RootState) => state.plants.data.length,
    }),
    plantCount(): string {
      return this.plants ? String(this.plants) : this.plantCountFromLocalStorage
    },
    plantCountLabel(): string {
      return this.plantCount === '1' ? 'Plant friend' : 'Plant friends'
    },
    displayName(): string {
      return this.userName || this.email
    },
  },

  methods: {
    ...mapActions({
      logout: 'account/signOutUser',
      showNotification: 'notifications/show',
    }),
    async logoutUser() {
      this.logoutRedirect = true
      try {
        await this.logout()
        await delay(2000)
        this.$store.commit('plants/resetState', null, { root: true })
        this.$store.commit('tags/resetState', null, { root: true })
        this.$router.push('/welcome')
      } catch (error) {
        this.logoutRedirect = false
        this.showNotification({ info: 'alert', message: error.message })
      }
    },
  },
})
