


































import Vue, { VueConstructor } from 'vue'
import { mapActions, mapState } from 'vuex'
import { AccountState } from '@/modules/account/store/state'
import setErrorMessage from '@/utils/setErrorMessage'

interface ProfileMapState {
  _displayName: string
  _photoURL: string
}

export default (Vue as VueConstructor<Vue & ProfileMapState>).extend({
  name: 'SettingsProfile',
  components: {
    'feather-loader': () =>
      import('vue-feather-icons/icons/LoaderIcon' /* webpackChunkName: "icons" */),
  },
  data() {
    return {
      name: null,
      photo: null,
      updateProgress: false,
      error: { el: null, message: null },
    }
  },
  computed: {
    ...mapState<AccountState>({
      _displayName: (state: AccountState) => state.displayName,
      _photoURL: (state: AccountState) => state.photoURL,
    }),
  },
  methods: {
    ...mapActions({ update: 'account/updateProfile' }),
    // TODO: Uploading a new photo does nothing right now. Feature needs to be implemented separately.
    updateProfile() {
      if (!this.name) return
      this.updateProgress = true
      try {
        this.update({
          displayName: this.name || this._displayName,
          photoURL: this.photo || this._photoURL,
        })
      } catch (error) {
        this.error = setErrorMessage(error)
      } finally {
        this.updateProgress = false
      }
    },
  },
})
